@charset "UTF-8";
*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  font-family: "Ubuntu Mono", monospace;
  display: grid;
  width: 100vw;
  height: 100vh;
  background-color: black;
  min-width: 320px;
  color: white;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.close {
  display: none;
}

.open {
  position: relative;
  width: 90vw;
  height: fit-content;
  background-color: rgb(4, 0, 122);
  outline: solid 5px rgb(0, 204, 255);
  margin: auto;
  padding: 5vh 20px;
  min-width: fit-content;
}

.scroll-lock {
  overflow: hidden;
  scrollbar-gutter: stable;
}

.modal__buttons-wrapper {
  display: flex;
}

.modal__button {
  cursor: pointer;
  font-family: inherit;
  font-weight: 700;
  font-size: calc(16px + 1.25vw);
  text-transform: uppercase;
  color: white;
  width: 30vw;
  height: 10vw;
  max-height: 110px;
  display: block;
  margin: 0 auto;
  border: 2px solid white;
  border-radius: 5px;
}
.modal__button--start {
  background-color: rgb(122, 201, 4);
}
.modal__button--info {
  background-color: orange;
}
.modal__button:hover {
  opacity: 0.9;
}
.modal__button:active {
  transform: scale(0.8);
}
@media (max-width: 510px) {
  .modal__button {
    font-size: calc(16px + 1.25vw);
    width: 26vw;
    height: 10vw;
  }
}

.modal__settings {
  position: absolute;
  right: 2vw;
  top: 2vh;
}

.game__color-word {
  display: block;
  font-size: 1.5em;
}
.game__color-word:hover {
  animation-name: rotate-word;
  animation-duration: 2s;
}

@keyframes rotate-word {
  from {
    transform: rotateY(0);
  }
  50% {
    transform: rotateY(180deg);
  }
  to {
    transform: rotateY(0);
  }
}
.game__wrapper {
  position: absolute;
  right: 2vw;
  top: 2vh;
  display: flex;
  flex-direction: column;
  gap: 1vh;
}

.green {
  background-color: green;
}

.red {
  background-color: red;
}

.yellow {
  background-color: yellow;
}

.pink {
  background-color: hotpink;
}

.blue {
  background-color: rgb(0, 195, 255);
}

.orange {
  background-color: orange;
}

.violet {
  background-color: rgb(155, 11, 187);
}

.beige {
  background-color: rgb(230, 218, 166);
}

.brown {
  background-color: rgb(150, 75, 0);
}

.green-word {
  color: green;
}

.red-word {
  color: red;
}

.yellow-word {
  color: yellow;
}

.pink-word {
  color: hotpink;
}

.blue-word {
  color: rgb(0, 195, 255);
}

.orange-word {
  color: orange;
}

.violet-word {
  color: rgb(155, 11, 187);
}

.beige-word {
  color: rgb(230, 218, 166);
}

.brown-word {
  color: rgb(150, 75, 0);
}

.instruction {
  font-size: calc(32px + 1.25vw);
  font-weight: 700;
  text-align: center;
  width: fit-content;
  margin: 0 auto;
  margin-bottom: 5vh;
}
@media (max-width: 768px) {
  .instruction {
    font-size: calc(24px + 1.25vw);
  }
}
@media (max-width: 510px) {
  .instruction {
    font-size: calc(24px + 1.25vw);
  }
}

.button {
  cursor: pointer;
  width: 60px;
  height: 60px;
  background-repeat: no-repeat;
  background-size: 50px 50px;
  background-color: transparent;
  background-position: center;
  border: none;
}
.button--close {
  background-image: url(../img/close.png);
}
.button--back {
  background-image: url(../img/restart.png);
  order: 2;
}
.button--pause {
  background-image: url(../img/pause.png);
}
.button--settings {
  background-image: url(../img/settings.png);
}
.button--settings:hover::after {
  display: block;
  position: relative;
  width: 14vw;
  min-width: 70px;
  height: 6vh;
  bottom: 4vh;
  right: 15vw;
  content: "В разработке!";
  color: #000;
  background-color: tomato;
  padding: 2vh;
  font-size: 8px;
  border-radius: 25px;
}
@media (max-width: 760px) {
  .button {
    width: 40px;
    height: 40px;
    background-size: 30px 30px;
  }
}
@media (max-width: 560px) {
  .button {
    width: 25px;
    height: 25px;
    background-size: 20px 20px;
  }
}

.dialog {
  background-color: rgb(4, 0, 122);
  border: 10px solid white;
  width: 90vw;
  padding: 0;
  color: #fff;
}
.dialog::backdrop {
  background-color: black;
}

.dialog__wrapper {
  padding: 5vh 5vw;
}

.dialog__close {
  position: absolute;
  right: 2vw;
  top: 2vh;
  display: flex;
  flex-direction: column;
  gap: 1vh;
}

.dialog__img {
  width: 100%;
  height: auto;
}

.dialog__description {
  line-height: 1.5;
  font-size: calc(16px + 1.25vw);
}

.dialog__color-word {
  display: block;
  font-size: 1.5em;
}

.dialog__buttons-wrapper {
  margin-bottom: 30px;
}

.dialog__note {
  display: none;
  position: absolute;
  width: 60px;
  height: 60px;
}
.dialog__note--correct {
  display: block;
  background-repeat: no-repeat;
  background-size: 50px 50px;
  background-color: transparent;
  background-position: center;
  border: none;
  background-image: url(../img/correct.png);
  animation-name: show-correct;
  animation-duration: 2s;
}
.dialog__note--falsy {
  display: block;
  width: 100px;
  height: fit-content;
}

@keyframes show-correct {
  from {
    transform: rotate(-90deg);
  }
  25% {
    transform: rotate(90deg);
  }
  50% {
    transform: rotate(-45deg);
  }
  75% {
    transform: rotate(45deg);
  }
  to {
    transform: rotate(0);
  }
}
.wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.title--big {
  font-size: calc(32px + 1.25vw);
  font-weight: 700;
  text-align: center;
  width: fit-content;
  margin: 0 auto;
  margin-bottom: 5vh;
}
@media (max-width: 768px) {
  .title--big {
    font-size: calc(24px + 1.25vw);
  }
}
@media (max-width: 510px) {
  .title--big {
    font-size: calc(24px + 1.25vw);
  }
}

.title--small {
  font-size: 1.5em;
  margin: 0;
}

.play-button {
  cursor: pointer;
  width: 10vw;
  height: 5vw;
  max-height: 55px;
  border: 3px solid black;
  box-shadow: -5px 4px 0 0 black;
  border-radius: 5px;
}
.play-button--correct:hover {
  transform: scale(1.5);
}

.statistics__container {
  display: block;
  font-size: calc(32px + 1.25vw);
  color: white;
  white-space: nowrap;
  margin: 0;
  padding-left: 4vw;
}
@media (max-width: 510px) {
  .statistics__container {
    font-size: calc(14px + 1.25vw);
  }
}
@media (max-width: 360px) {
  .statistics__container {
    font-size: calc(10px + 1.25vw);
  }
}

.statistics__wrapper {
  display: block;
}

.statistics__key {
  display: inline;
}

.statistics__value {
  display: inline;
  margin: 0;
  padding: 0;
}

.statistics__close {
  position: absolute;
  right: 2vw;
  top: 2vh;
  display: flex;
  flex-direction: column;
  gap: 1vh;
}

.settings {
  max-height: 55vh;
}

.settings__main-wrapper {
  display: grid;
  grid-template-areas: "title title title" "timer . click" "timer-range button click-range";
  grid-gap: 3vh;
}

.settings__title {
  margin-bottom: 3vh;
  grid-area: title;
}

.settings__subtitle {
  font-size: calc(28px + 1.25vw);
}

.settings__container {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 1vh;
}
.settings__container--timer {
  grid-area: timer;
}
.settings__container--clicks {
  grid-area: click;
}

.settings__submit {
  grid-area: button;
  width: 15vw;
  height: 5vw;
  max-height: 110px;
  min-height: 5vh;
  margin: 0 auto;
  display: block;
  border: 2px solid white;
  border-radius: 5px;
  background-color: rgb(122, 201, 4);
  font-size: calc(16px + 1.25vw);
  color: #fff;
}

.settings__close {
  position: absolute;
  right: 2vw;
  top: 2vh;
}

.settings__radio-wrapper {
  display: flex;
  justify-content: center;
  gap: 5vw;
}

.settings__label {
  position: relative;
  font-size: calc(22px + 1.25vw);
}

.settings__control-label {
  padding-right: 1vw;
}

.settings__control {
  cursor: pointer;
  position: absolute;
  width: 3vw;
  height: 3vw;
  border: 0;
  border-radius: 5px;
  background-color: #fff;
}

.settings__range {
  display: none;
  width: 15vw;
  height: 5vw;
  min-height: 5vh;
  max-height: 110px;
  margin: 0 auto;
  border-radius: 5px;
  border: 2px solid #7ac904;
  font-size: calc(22px + 1.25vw);
}
.settings__range--clicks {
  grid-area: click-range;
}
.settings__range--timer {
  grid-area: timer-range;
}

.settings__radio[type=radio]:checked ~ .settings__control {
  background-image: url("../img/correct.png");
  background-repeat: no-repeat;
  width: 3vw;
  height: 3vw;
  background-size: 2vw 2vw;
  background-position: center;
}